import { css } from 'glamor'

const style = {
  wrap: css(
    {
      background: '#2B2D30',
      padding: '0 8rem',
      position: 'relative',
      width: '100%',
    },
  ),
  wrapInner: css(
    {
      borderTop: '0.2rem solid #FF9900',
      borderBottom: '0.2rem solid #FF9900',
      display: 'flex',
      margin: 'auto',
      maxWidth: '120.6rem',
      position: 'relative',
      width: '100%',
    },
  ),
  button: css(
    {
      color: '#fff',
      cursor: 'pointer',
      display: 'block',
      fontSize: '1.6rem',
      fontWeight: '700',
      flex: '1',
      letterSpacing: '0.1em',
      lineHeight: '7rem',
      outline: 'none',
      textTransform: 'uppercase',
      textDecoration: 'none',
      transition: 'opacity 0.4s',
      ':last-child': {
        marginLeft: 'auto',
        textAlign: 'right',
      },
      ':hover': {
        opacity: '0.6',
      },
    },
  ),
}

export default style



